<template>
    <div class="myCollection">
        <div class="header">
            <span @click="back" class="back">
                <img src="../../assets/img/back.png">
            </span>
            <span class="usersTitle">收藏</span>
            <span class="back"></span>
        </div>
        <div class="content">
        <div class="row" v-for="(collection, index) in collections" :key="index">
          <div class="rowHead">
            <span class="publish">
              <div class="headPic">
                <img :src="collection.fLogo" class="headImg">
              </div>
              <div class="publishInfor">
                <span class="publisher">{{ collection.fNickname }}</span>
                <span class="publishTime">{{ collection.fCreatedTime }}</span>
              </div>
              <span class="cancleCollect" @click="cancleCollect(index)" v-if="userClick === 'self'">取消收藏</span>
            </span>
          </div>
          <div class="blog">{{ collection.fMontNote }}</div>
            <div :class="collection.medias.length === 4 || collection.medias.length === 1 ? picArea2 : picArea1" v-if="collection.fCatType === '0'">
              <span :class="collection.medias.length === 1 ? img1 : img3" v-for="(media, index) in collection.medias" :key="index">
                <img :src="media.media">
              </span>
            </div>
            <div class="videoArea" v-if="collection.fCatType === '1'">
              <video class="picArea2" controls :poster="collection.fMedia1">
                <source :src="collection.fMedia2">
              </video>
            </div>
          </div>
      </div>
    </div>
</template>

<script>
import { getCollectionList, enshrine } from '../../api/getdata'
import store from '../../vuex/store'
import { Toast } from 'mint-ui'
export default {
  name: 'myCollection',
  data () {
    return {
      img1: 'img1',
      img3: 'img3',
      picArea1: 'picArea1',
      picArea2: 'picArea2',
      collections: [],
      userClick: ''
    }
  },
  mounted () {
    this.getCollections()
    console.log(store.state.userClick)
    this.userClick = store.state.userClick
  },
  methods: {
    back () {
      this.$router.go(-1)
    },
    getCollections () {
      let data = getCollectionList({fUserId: store.state.userId})
      data.then(res => {
        console.log(res.data)
        this.collections = res.data
      })
    },
    cancleCollect (index) {
      console.log('取消收藏')
      let data = enshrine({state: 0, fMontId: this.collections[index].fMontId})
      data.then(res => {
        console.log(res)
        if (res.status === '0') {
          Toast({
            message: '已取消收藏',
            duration: 1500
          })
          this.collections.splice(index, 1)
        } else {
          Toast({
            message: '取消失败，请稍后再试',
            duration: 1500
          })
        }
      })
    }
  }
}
</script>

<style scoped>
 * {
    margin: 0;
    padding: 0;
    color: #333333;
  }

  .header {
    width: 100%;
    height: 1.5rem;
    display: flex;
    justify-content: space-between;
    position: fixed;
    left: 0;
    top: 0;
    font-size: 0.48rem;
    background: #fff;
    z-index: 99;
  }

  .header span {
    display: flex;
    justify-content: center;
    padding: 0.5rem;
    line-height: 0.5rem;
  }

  .back {
    height: 0.5rem;
    width: 0.266667rem;
  }

  img {
    width: 100%;
  }

  .content {
    width: 100%;
    margin-top: 1.5rem;
  }

  .row {
    width: calc(100% - 0.625rem);
    margin: 0 auto;
    margin-top: 0.74667rem;
    border-bottom: 0.1rem solid #f5f5f5;
  }

  .picArea2::after,
  .picArea1::after {
    content: "";
    display: block;
    clear: both;
  }

  .rowHead {
    display: flex;
    align-items: center;
    justify-content: space-between;
    position: relative;
  }

  .publish {
    display: flex;
    align-items: center;
  }

  .publishInfor {
    text-align: left;
    margin-left: 0.5rem;
    display: flex;
    flex-direction: column;
    justify-content: space-between;
  }

  .headPic {
    width: 1.6rem;
    height: 1.6rem;
    border-radius: 50%;
    position: relative;
  }

  .headPic img {
    border-radius: 0.8rem;
  }

  .publisher {
    font-size: 0.42667rem;
    color: #333333;
    display: flex;
    justify-content: flex-start;
    align-items: center;
  }

  .publishTime {
    color: #666666;
    font-size: 0.32rem;
    display: flex;
    justify-content: center;
    align-items: center;
    margin-top: 0.3rem;
  }

  .blog {
    margin-top: 0.5rem;
    text-align: start;
    font-size: 0.4rem;
    color: #333333;
  }

  .picArea1,
  .picArea2 {
    margin-top: 0.4rem;
  }

  .picArea2 {
    width: 66.667%;
  }

  .img3 {
    display: block;
    float: left;
    width: 2.96rem;
    height: 1.947rem;
    margin: 0.12rem 0.08rem;
  }

  img {
    width: 100%;
    height: 100%;
  }

  .headImg {
    width: 100%;
    height: 100%;
  }

  .img1 {
    width: 6rem;
  }

 .usertype {
    display: flex;
    justify-content: center;
    align-items: center;
    position: absolute;
    right: -0.1rem;
    bottom: -0.1rem;
    color: #fffcfc;
    width: 0.6rem;
    height: 0.6rem;
    border-radius: 50%;
    border: 2px solid #fffcfc;
    background: #ffd200;
    font-size: 0.373333rem;
  }

  .videoArea {
    text-align: start;
  }

  .cancleCollect {
    font-size: 0.4rem;
    color: #666666;
    position: absolute;
    right: 0;
    top: 0.3rem;
  }
</style>



